	module.exports = {"data":{"postsJson":{"title":"God Came to Us","questionsTitle":"HUNGERING TO KNOW GOD MORE","display":"block","bookName":"John - ΚΑΤΑ ΙΩΑΝΝΗΝ","ref":"1:1-1:51","html":"<p>   <strong>1</strong><u>1</u>Ἐν ἀρχῇ ἦν ὁ λόγος, καὶ ὁ λόγος ἦν πρὸς τὸν θεόν, καὶ θεὸς ἦν ὁ λόγος. <u>2</u>οὗτος ἦν ἐν ἀρχῇ πρὸς τὸν θεόν. <u>3</u>πάντα διʼ αὐτοῦ ἐγένετο, καὶ χωρὶς αὐτοῦ ἐγένετο οὐδὲ ἕν. ὃ γέγονεν <u>4</u>ἐν αὐτῷ ζωὴ ἦν, καὶ ἡ ζωὴ ἦν τὸ φῶς τῶν ἀνθρώπων· <u>5</u>καὶ τὸ φῶς ἐν τῇ σκοτίᾳ φαίνει, καὶ ἡ σκοτία αὐτὸ οὐ κατέλαβεν.<br />   <u>6</u>Ἐγένετο ἄνθρωπος, ἀπεσταλμένος παρὰ θεοῦ, ὄνομα αὐτῷ Ἰωάννης·<u>7</u>οὗτος ἦλθεν εἰς μαρτυρίαν ἵνα μαρτυρήσῃ περὶ τοῦ φωτός, ἵνα πάντες πιστεύσωσιν διʼ αὐτοῦ. <u>8</u>οὐκ ἦν ἐκεῖνος τὸ φῶς, ἀλλʼ ἵνα μαρτυρήσῃ περὶ τοῦ φωτός.<br />   <u>9</u>Ἦν τὸ φῶς τὸ ἀληθινόν, ὃ φωτίζει πάντα ἄνθρωπον, ἐρχόμενον εἰς τὸν κόσμον. <u>10</u>ἐν τῷ κόσμῳ ἦν, καὶ ὁ κόσμος διʼ αὐτοῦ ἐγένετο, καὶ ὁ κόσμος αὐτὸν οὐκ ἔγνω. <u>11</u>εἰς τὰ ἴδια ἦλθεν, καὶ οἱ ἴδιοι αὐτὸν οὐ παρέλαβον. <u>12</u>ὅσοι δὲ ἔλαβον αὐτόν, ἔδωκεν αὐτοῖς ἐξουσίαν τέκνα θεοῦ γενέσθαι, τοῖς πιστεύουσιν εἰς τὸ ὄνομα αὐτοῦ,<u>13</u>οἳ οὐκ ἐξ αἱμάτων οὐδὲ ἐκ θελήματος σαρκὸς οὐδὲ ἐκ θελήματος ἀνδρὸς ἀλλʼ ἐκ θεοῦ ἐγεννήθησαν.<br />   <u>14</u>Καὶ ὁ λόγος σὰρξ ἐγένετο καὶ ἐσκήνωσεν ἐν ἡμῖν, καὶ ἐθεασάμεθα τὴν δόξαν αὐτοῦ, δόξαν ὡς μονογενοῦς παρὰ πατρός, πλήρης χάριτος καὶ ἀληθείας. <u>15</u>Ἰωάννης μαρτυρεῖ περὶ αὐτοῦ καὶ κέκραγεν λέγων·</p><h1>JOHN THE BAPTIST </h1><h4>οὗτος ἦν ὃν εἶπον· ὁ ὀπίσω μου ἐρχόμενος ἔμπροσθέν μου γέγονεν, ὅτι πρῶτός μου ἦν.</h4><p><u>16</u>ὅτι ἐκ τοῦ πληρώματος αὐτοῦ ἡμεῖς πάντες ἐλάβομεν καὶ χάριν ἀντὶ χάριτος· <u>17</u>ὅτι ὁ νόμος διὰ Μωϋσέως ἐδόθη, ἡ χάρις καὶ ἡ ἀλήθεια διὰ Ἰησοῦ Χριστοῦ ἐγένετο. <u>18</u>Θεὸν οὐδεὶς ἑώρακεν πώποτε· μονογενὴς θεὸς ὁ ὢν εἰς τὸν κόλπον τοῦ πατρὸς ἐκεῖνος ἐξηγήσατο.<br />   <u>19</u>Καὶ αὕτη ἐστὶν ἡ μαρτυρία τοῦ Ἰωάννου, ὅτε ἀπέστειλαν πρὸς αὐτὸν οἱ Ἰουδαῖοι ἐξ Ἱεροσολύμων ἱερεῖς καὶ Λευίτας ἵνα ἐρωτήσωσιν αὐτόν·</p><h1>JEWISH LEADERS’ ENVOYS </h1><h4>σὺ τίς εἶ;</h4><p><u>20</u>καὶ ὡμολόγησεν καὶ οὐκ ἠρνήσατο, καὶ ὡμολόγησεν ὅτι</p><h1>JOHN THE BAPTIST </h1><h4>ἐγὼ οὐκ εἰμὶ ὁ χριστός.</h4><p><u>21</u>καὶ ἠρώτησαν αὐτόν·</p><h1>JEWISH LEADERS’ ENVOYS </h1><h4>τί οὖν; σὺ Ἠλίας εἶ;</h4><p>καὶ λέγει·</p><h1>JOHN THE BAPTIST </h1><h4>οὐκ εἰμί.</h4><h1>JEWISH LEADERS’ ENVOYS </h1><h4>ὁ προφήτης εἶ σύ;</h4><p>καὶ ἀπεκρίθη·</p><h1>JOHN THE BAPTIST </h1><h4>οὔ.</h4><p><u>22</u>εἶπαν οὖν αὐτῷ·</p><h1>JEWISH LEADERS’ ENVOYS </h1><h4>τίς εἶ; ἵνα ἀπόκρισιν δῶμεν τοῖς πέμψασιν ἡμᾶς· τί λέγεις περὶ σεαυτοῦ;</h4><p><u>23</u>ἔφη·</p><h1>JOHN THE BAPTIST </h1><h4>ἐγὼ φωνὴ βοῶντος ἐν τῇ ἐρήμῳ·<br />   εὐθύνατε τὴν ὁδὸν κυρίου,<br />   καθὼς εἶπεν Ἠσαΐας ὁ προφήτης.</h4><p>.  <u>24</u>Καὶ ἀπεσταλμένοι ἦσαν ἐκ τῶν Φαρισαίων. <u>25</u>καὶ ἠρώτησαν αὐτὸν καὶ εἶπαν αὐτῷ·</p><h1>JEWISH LEADERS’ ENVOYS </h1><h4>τί οὖν βαπτίζεις εἰ σὺ οὐκ εἶ ὁ χριστὸς οὐδὲ Ἠλίας οὐδὲ ὁ προφήτης;</h4><p><u>26</u>ἀπεκρίθη αὐτοῖς ὁ Ἰωάννης λέγων·</p><h1>JOHN THE BAPTIST </h1><h4>ἐγὼ βαπτίζω ἐν ὕδατι· μέσος ὑμῶν ἕστηκεν ὃν ὑμεῖς οὐκ οἴδατε,<u>27</u>ὁ ὀπίσω μου ἐρχόμενος , οὗ οὐκ εἰμὶ ἐγὼ ἄξιος ἵνα λύσω αὐτοῦ τὸν ἱμάντα τοῦ ὑποδήματος.</h4><p><u>28</u>ταῦτα ἐν Βηθανίᾳ ἐγένετο πέραν τοῦ Ἰορδάνου, ὅπου ἦν ὁ Ἰωάννης βαπτίζων.<br />   <u>29</u>Τῇ ἐπαύριον βλέπει τὸν Ἰησοῦν ἐρχόμενον πρὸς αὐτὸν καὶ λέγει·</p><h1>JOHN THE BAPTIST </h1><h4>ἴδε ὁ ἀμνὸς τοῦ θεοῦ ὁ αἴρων τὴν ἁμαρτίαν τοῦ κόσμου. <u>30</u>οὗτός ἐστιν ὑπὲρ οὗ ἐγὼ εἶπον· ὀπίσω μου ἔρχεται ἀνὴρ ὃς ἔμπροσθέν μου γέγονεν, ὅτι πρῶτός μου ἦν. <u>31</u>κἀγὼ οὐκ ᾔδειν αὐτόν, ἀλλʼ ἵνα φανερωθῇ τῷ Ἰσραὴλ διὰ τοῦτο ἦλθον ἐγὼ ἐν ὕδατι βαπτίζων.</h4><p><u>32</u>Καὶ ἐμαρτύρησεν Ἰωάννης λέγων ὅτι</p><h1>JOHN THE BAPTIST </h1><h4>τεθέαμαι τὸ πνεῦμα καταβαῖνον ὡς περιστερὰν ἐξ οὐρανοῦ καὶ ἔμεινεν ἐπʼ αὐτόν. <u>33</u>κἀγὼ οὐκ ᾔδειν αὐτόν, ἀλλʼ ὁ πέμψας με βαπτίζειν ἐν ὕδατι ἐκεῖνός μοι εἶπεν· ἐφʼ ὃν ἂν ἴδῃς τὸ πνεῦμα καταβαῖνον καὶ μένον ἐπʼ αὐτόν, οὗτός ἐστιν ὁ βαπτίζων ἐν πνεύματι ἁγίῳ. <u>34</u>κἀγὼ ἑώρακα καὶ μεμαρτύρηκα ὅτι οὗτός ἐστιν ὁ υἱὸς τοῦ θεοῦ.</h4><p>   <u>35</u>Τῇ ἐπαύριον πάλιν εἱστήκει ὁ Ἰωάννης καὶ ἐκ τῶν μαθητῶν αὐτοῦ δύο <u>36</u>καὶ ἐμβλέψας τῷ Ἰησοῦ περιπατοῦντι λέγει·</p><h1>JOHN THE BAPTIST </h1><h4>ἴδε ὁ ἀμνὸς τοῦ θεοῦ.</h4><p><u>37</u>καὶ ἤκουσαν οἱ δύο μαθηταὶ αὐτοῦ λαλοῦντος καὶ ἠκολούθησαν τῷ Ἰησοῦ. <u>38</u>στραφεὶς δὲ ὁ Ἰησοῦς καὶ θεασάμενος αὐτοὺς ἀκολουθοῦντας λέγει αὐτοῖς·</p><h1>JESUS </h1><h2>τί ζητεῖτε;</h2><p>οἱ δὲ εἶπαν αὐτῷ·</p><h1>ANDREW AND ANOTHER DISCIPLE </h1><h3>ῥαββί,</h3><p>ὃ λέγεται μεθερμηνευόμενον διδάσκαλε,</p><h1>ANDREW AND ANOTHER DISCIPLE </h1><h3>ποῦ μένεις;</h3><p><u>39</u>λέγει αὐτοῖς·</p><h1>JESUS </h1><h2>ἔρχεσθε καὶ ὄψεσθε.</h2><p>ἦλθαν οὖν καὶ εἶδαν ποῦ μένει καὶ παρʼ αὐτῷ ἔμειναν τὴν ἡμέραν ἐκείνην· ὥρα ἦν ὡς δεκάτη. <u>40</u>Ἦν Ἀνδρέας ὁ ἀδελφὸς Σίμωνος Πέτρου εἷς ἐκ τῶν δύο τῶν ἀκουσάντων παρὰ Ἰωάννου καὶ ἀκολουθησάντων αὐτῷ·<u>41</u>εὑρίσκει οὗτος πρῶτον τὸν ἀδελφὸν τὸν ἴδιον Σίμωνα καὶ λέγει αὐτῷ·</p><h1>ANDREW </h1><h3>εὑρήκαμεν τὸν Μεσσίαν,</h3><p>ὅ ἐστιν μεθερμηνευόμενον χριστός. <u>42</u>ἤγαγεν αὐτὸν πρὸς τὸν Ἰησοῦν. ἐμβλέψας αὐτῷ ὁ Ἰησοῦς εἶπεν·</p><h1>JESUS </h1><h2>σὺ εἶ Σίμων ὁ υἱὸς Ἰωάννου, σὺ κληθήσῃ Κηφᾶς,</h2><p>ὃ ἑρμηνεύεται Πέτρος.<br />   <u>43</u>Τῇ ἐπαύριον ἠθέλησεν ἐξελθεῖν εἰς τὴν Γαλιλαίαν καὶ εὑρίσκει Φίλιππον. καὶ λέγει αὐτῷ ὁ Ἰησοῦς·</p><h1>JESUS </h1><h2>ἀκολούθει μοι.</h2><p><u>44</u>ἦν δὲ ὁ Φίλιππος ἀπὸ Βηθσαϊδά, ἐκ τῆς πόλεως Ἀνδρέου καὶ Πέτρου. <u>45</u>εὑρίσκει Φίλιππος τὸν Ναθαναὴλ καὶ λέγει αὐτῷ·</p><h1>PHILIP THE DISCIPLE </h1><h3>ὃν ἔγραψεν Μωϋσῆς ἐν τῷ νόμῳ καὶ οἱ προφῆται εὑρήκαμεν, Ἰησοῦν υἱὸν τοῦ Ἰωσὴφ τὸν ἀπὸ Ναζαρέτ.</h3><p><u>46</u>καὶ εἶπεν αὐτῷ Ναθαναήλ·</p><h1>NATHANAEL – BARTHOLOMEW </h1><h3>ἐκ Ναζαρὲτ δύναταί τι ἀγαθὸν εἶναι;</h3><p>λέγει αὐτῷ ὁ Φίλιππος·</p><h1>PHILIP THE DISCIPLE </h1><h3>ἔρχου καὶ ἴδε.</h3><p><u>47</u>Εἶδεν ὁ Ἰησοῦς τὸν Ναθαναὴλ ἐρχόμενον πρὸς αὐτὸν καὶ λέγει περὶ αὐτοῦ·</p><h1>JESUS </h1><h2>ἴδε ἀληθῶς Ἰσραηλίτης ἐν ᾧ δόλος οὐκ ἔστιν.</h2><p><u>48</u>λέγει αὐτῷ Ναθαναήλ·</p><h1>NATHANAEL – BARTHOLOMEW </h1><h3>πόθεν με γινώσκεις;</h3><p>ἀπεκρίθη Ἰησοῦς καὶ εἶπεν αὐτῷ·</p><h1>JESUS </h1><h2>πρὸ τοῦ σε Φίλιππον φωνῆσαι ὄντα ὑπὸ τὴν συκῆν εἶδόν σε.</h2><p><u>49</u>ἀπεκρίθη αὐτῷ Ναθαναήλ·</p><h1>NATHANAEL – BARTHOLOMEW </h1><h3>ῥαββί, σὺ εἶ ὁ υἱὸς τοῦ θεοῦ, σὺ βασιλεὺς εἶ τοῦ Ἰσραήλ.</h3><p><u>50</u>ἀπεκρίθη Ἰησοῦς καὶ εἶπεν αὐτῷ·</p><h1>JESUS </h1><h2>ὅτι εἶπόν σοι ὅτι εἶδόν σε ὑποκάτω τῆς συκῆς, πιστεύεις; μείζω τούτων ὄψῃ.</h2><p><u>51</u>καὶ λέγει αὐτῷ·</p><h1>JESUS </h1><h2>ἀμὴν ἀμὴν λέγω ὑμῖν, ὄψεσθε τὸν οὐρανὸν ἀνεῳγότα καὶ τοὺς ἀγγέλους τοῦ θεοῦ ἀναβαίνοντας καὶ καταβαίνοντας ἐπὶ τὸν υἱὸν τοῦ ἀνθρώπου.</h2>","path":"/340","Question1":"Take note of the acts of God described in this story.","Question2":"What do these reveal about the character of God?","Question3":"Have you experienced this dimension of the character of God in your life? If yes, how so? If not, how might you come to experience this?","Question4":"What can you do to grow in your desire to know and experience God more intimately today?","prevPath":"/339","nextPath":"/341"}},"pathContext":{}}

